.loginsignup{
    width: 100%;
    height:80v ;
    background: #fce3fe;
    padding-top: 100px;
    padding-bottom: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loginsignup-container{
    width:580px;
    max-width: 90%;
    height: auto;
    background: white;
    margin: auto;
    padding: 40px 60px;
    box-sizing: border-box;
}
.loginsignup-container h1{
    margin: 20px 0px;
    font-size: 28px;
    text-align: center;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top:30px;
}
.loginsignup-fields input{
    height: 60px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 16px;
    box-sizing: border-box;
}
.loginsignup-container button{
    width: 100%;
    height: 60px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
}

.loginsignup-login span{
    color: #ff4141;
    font-weight: 600;
    cursor: pointer;
}
.loginsignup-agree{
    display: flex;
    align-items: flex-start;
    margin-top: 25px;
    gap: 10px;
    color: #5c5c5c;
    font-size: 14px;
    font-weight: 500;
    flex-wrap: wrap;
}

/* RESPONSIVE BREAKPOINTS */
@media (max-width: 480px) {
  .loginsignup-container {
    padding: 30px 20px;
  }

  .loginsignup-fields input {
    height: 50px;
    font-size: 15px;
  }

  .loginsignup-container button {
    height: 50px;
    font-size: 18px;
  }

  .loginsignup-agree {
    font-size: 13px;
  }
}