/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
  }
  .nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .nav-logo p{
    color:#171717;
    font-size: 38px;
    font-weight: 600;
  }
  .nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
  }
  .nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
  }
  .nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
  }
  .nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
  }
  .nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
  }
  .nav-login-cart button:active{
    background: #f3f3f3;
  }
  .nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    column-gap: white;
  }

  /* For tablets and smaller desktops */
@media (max-width: 900px) {
  .navbar {
    justify-content: space-between;
    padding: 12px 20px;
  }
  .nav-logo p {
    font-size: 28px;
  }
  .nav-menu {
    gap: 30px;
    font-size: 18px;
  }
  .nav-login-cart {
    gap: 25px;
  }
  .nav-login-cart button {
    width: 120px;
    height: 45px;
    font-size: 16px;
  }
}

/* For mobile phones */
@media (max-width: 600px) {
  .navbar {
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px 15px;
  }
  .nav-logo {
    flex: 1 1 100%;
    justify-content: center;
  }
  .nav-logo p {
    font-size: 24px;
  }
  .nav-menu {
    flex: 1 1 100%;
    justify-content: center;
    gap: 20px;
    font-size: 16px;
  }
  .nav-login-cart {
    flex: 1 1 100%;
    justify-content: center;
    gap: 20px;
    margin-top: 8px;
  }
  .nav-login-cart button {
    width: 100px;
    height: 40px;
    font-size: 14px;
  }
  .nav-cart-count {
    margin-top: -28px;
    margin-left: -45px;
  }
}
