.hero{
    height: 100vh;
    background: linear-gradient(180deg ,#fde1ff,#e1ffea22 60%);
    display: flex;
    padding: 40px 0;
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}
.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p{
    color: #171717;
    font-size: 100px;
    font-weight: 700;
    margin: 0;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img{
    width: 105px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
}
.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* ------------------ RESPONSIVE ------------------ */

@media (max-width: 1024px) {
  .hero-left {
    padding-left: 80px;
  }
  
  .hero-left p {
    font-size: 70px;
  }
  
  .hero-latest-btn {
    width: 250px;
    height: 60px;
    font-size: 18px;
  }
  
  .hero-hand-icon img {
    width: 80px;
  }
}

@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    height: auto;
    padding: 40px 20px;
  }

  .hero-left {
    padding-left: 0;
    text-align: center;
  }

  .hero-left p {
    font-size: 50px;
  }

  .hero-latest-btn {
    margin: 20px auto 0 auto;
    width: 220px;
    height: 55px;
    font-size: 16px;
  }

  .hero-right {
    margin-top: 40px;
  }

  .hero-hand-icon {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .hero-left h2 {
    font-size: 20px;
  }

  .hero-left p {
    font-size: 38px;
  }

  .hero-hand-icon img {
    width: 60px;
  }

  .hero-latest-btn {
    width: 180px;
    height: 45px;
    font-size: 14px;
  }
}