.newcollections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-bottom: 100px;
}
.newcollections h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.newcollections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 30px;
}

/* For tablets */
@media (max-width: 900px) {
  .collections {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}

/* For mobile phones */
@media (max-width: 600px) {
  .collections {
    grid-template-columns: 1fr;
    gap: 15px;
  }
}